<template>
  <el-dialog :title="titleFont" :visible.sync="msg" :before-close="handleClose" class="c-maxbody">
    <el-form :model="data" label-width="120px" ref="ruleForm" :rules="rules" class="c-myForm">

      <el-form-item prop="name" label="">
        <el-input auto-complete="false" v-model="data.name" size="small"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-upload
          class="avatar-uploader"
          action=""
          :file-list="fileList"
          list-type="picture-card"
          :http-request="httpRequest"
          :on-remove="removeFile"
          :limit=1
        ><i class="el-icon-plus avatar-uploader-icon"></i></el-upload>
      </el-form-item>

      <el-form-item label="">
        <el-upload
          class="avatar-uploader"
          action=""
          :file-list="pictureList"
          list-type="picture-card"
          :http-request="httpRequest1"
          :on-remove="removeFile1"
        ><i class="el-icon-plus avatar-uploader-icon"></i></el-upload>
      </el-form-item>

      <el-form-item label="" prop="type">
        <el-select v-model="data.type" placeholder="" clearable size="small" @change="selectType">
          <el-option
            v-for="s in typeList"
            :key="s.id"
            :label="s.name"
            :value="s.code"
          >
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" prop="tag">
        <el-checkbox-group v-model="data.tag" size="small">
          <el-checkbox v-for="(t,index) in minTag" :label.number="t" :name="t" :key="index"
                       class="c-checkbox"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>

      <el-form-item label="">
        <el-time-select
          placeholder=""
          v-model="data.serviceBeginAt"
          :picker-options="{
          start: '00:00',
          step: '00:15',
          end: '24:00'
        }"></el-time-select>
        <el-time-select
          placeholder=""
          v-model="data.serviceEndAt"
          :picker-options="{
          start: '00:00',
          step: '00:15',
          end: '24:00',
          minTime: data.serviceBeginAt
        }">
        </el-time-select>
      </el-form-item>

      <el-form-item label="" prop="telPhone">
        <el-input type="number" v-model="data.telPhone" min="1" size="small"></el-input>
      </el-form-item>

      <el-form-item label="" prop="ThreeAddress">
        <el-cascader :options="options" v-model="data.ThreeAddress" size="small" @change="checkAdress">
        </el-cascader>
      </el-form-item>

      <el-form-item label="" prop="address">
        <el-select
          v-model="data.address"
          remote
          filterable
          reserve-keyword
          placeholder=""
          :remote-method="addressChange"
          :loading="addressLoading"
          @change="onChange"
          size="small">
          <el-option
            v-for="item in options4"
            :key="item.ids"
            :label="item.name"
            :value="item.name">
          </el-option>
        </el-select>
      </el-form-item>
      <div class="c-map">
        <div class="c-map-body" id="container"></div>
      </div>

      <el-form-item prop="communityIds" label="">
        <el-select
        v-model="data.communityIds"
        multiple
        placeholder="">
        <el-option
          v-for="item in communityIdsList"
          :key="item.id"
          :label="item.name"
          :value="item.id">
        </el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
    </div>
  </el-dialog>
</template>

<script>
  import cityData from "@/utils/citys"
  import {Map as map} from '@/utils/map'
  import {send, getUri} from '@/utils/oss'
  import tagList from '@/mock/tag'

  export default {
    name: "merchantForm",
    data() {
      var checkTag = (rule, value, cb) => {
        if (!value.length) {
          return cb(new Error(''))
        }
        if (value.length > 3) {
          return cb(new Error(''));
        }
      }
      return {
        autoUpload: false,
        time: null,
        titleFont: this.edata ? '':'',
        data: {
          type: null,
          tag: [],
          address: '',
          serviceBeginAt: '',
          serviceEndAt: '',
          picture: [],
          local: [],//
          communityIds:[]
        },
        typeList: [],
        minTag: null,//
        pictureList:[],//
        uploadFile_pic:[],//
        fileList: [],
        uploadFile: [],
        options: cityData,
        mapObj: null,
        options4: [],//
        addressLoading: false,//...
        communityIdsList: [{id:0,name:'allen'}],//
        timeOutVal: null,//,
        rules: {
          name: [{required: true, message: '', trigger: 'blur'}],
          type: [{required: true, message: '', trigger: 'blur'}],
          tag: [{validator: checkTag, trigger: 'change'}],
          ThreeAddress: [{type: 'array', required: true, message: '', trigger: 'change'}],
          telPhone: [{required: true, message: '', trigger: 'blur'}, {
            min: 11,
            max: 11,
            message: "11",
            trigger: 'blur'
          }],
          address: [{required: true, message: '', trigger: 'blur'}],
        },
      }
    },
    props: ['msg', 'edata'],
    created() {
      this.getTag();
      this.getCommunityIds();
      if (this.edata) {//
        this.data = this.edata;
        this.data.ThreeAddress = [this.edata.province, this.edata.city, this.edata.district || null];
        this.data.picture = this.data.picture ?this.data.picture : [];
        let arr = [this.edata.logo,...(this.edata.picture || [])];
        if(arr.length){//ossurl
            arr.forEach((item,index) => {
                if(!item)return;
              getUri(item, (uri) => {
                let o = {url: uri,key:item};
                if(index == 0){
                  this.fileList.push(o);
                }else {
                  this.pictureList.push(o);
                }
              })
            })
        }
      }
      this.$nextTick(() => {
        this.mapObj = new map({cb: this.getAddress});
        if (this.edata) {
          this.success(this.edata.address);
        }
      })
    },
    methods: {
      handleClose() {
        this.$emit("upList", 1);
      },
      submitForm() {
        let that = this;
        let arr = [...this.uploadFile,...this.uploadFile_pic];
        let length = arr.length;
        if (length) {
          let flag = 0;
          arr.forEach((item) => {
            send(item, (key) => {
              flag++;
              if(flag == 1 && this.uploadFile.length){
                  that.data.logo = key;
              }else {
                  this.data.picture.push(key);
              }
              if(flag == length) {
                that.sendAjax();
              }
            });
          });
        } else {
          that.sendAjax();
        }
      },
      getTag() {
        this.$xttp.get('/biz/shop/type/list')
          .then(res => {
            if (!res.errorCode) {
              this.typeList = res.data;
              if (this.edata) {
                this.selectType(false);
              }
            }
          })
      },
      getCommunityIds() {
        this.$xttp.post('/community/list',{})
          .then(res => {
            if (!res.errorCode) {
              this.communityIdsList = res.data;
            }
          })
      },
      sendAjax(){
        if (!this.data.name || !this.data.type  || !this.data.telPhone || !this.data.address )return;
        let msg = this.edata ? '' : '';
        let postUrl = this.edata ? '/biz/shop/edit' : '/biz/shop/add';
        this.$xttp.post(postUrl, this.data)
          .then(res => {
            if (!res.errorCode) {
              this.$message({
                message: msg + "",
                type: "success"
              });
              this.$emit("upList", 2);
            }
          })
      },
      removeFile(file) {
        this.uploadFile.splice(0, 1);
        if(this.edata) {
          this.data.logo = null;
        }
      },
      removeFile1(file) {
        let index = this.uploadFile_pic.findIndex(item => item.uid == file.uid);
        this.uploadFile_pic.splice(index, 1);
        if(this.edata) {
          let index = this.data.picture.findIndex(item => item == file.key);
          this.data.picture.splice(index, 1);
        }
      },
      httpRequest(file) {
        this.uploadFile.push(file.file);
      },
      httpRequest1(file) {
        this.uploadFile_pic.push(file.file);
      },
      checkAdress() {//
        this.data.province = this.data.ThreeAddress[0];
        this.data.city = this.data.ThreeAddress[1];
        this.data.district = this.data.ThreeAddress[2];
      },
      getAddress(obj) {// obj => 
        this.data.address = obj.address;
        this.data.local = [obj.lng, obj.lat];
      },
      success(address) {//
        this.mapObj.getLocation(address, event => {
        })
      },
      addressChange(val) {//
        clearTimeout(this.timeOutVal);
        if (val !== '') {
          this.addressLoading = true;
          this.timeOutVal = setTimeout(() => {
            this.mapObj.searchFont(val, event => {
              if (event.tips.length) {
                let num = 1;
                event.tips.forEach(item => item.ids = num++);
                this.options4 = event.tips;
                this.addressLoading = false;
              }
            });
          }, 500);
        }
      },
      onChange(val) {//
        let that = this;
        this.mapObj.getLocation(val, event => {
          that.data.local = [event.lng, event.lat];
        })
      },
      selectType(bool){
        if (bool) {
          this.data.tag = [];
        }
        this.minTag = this.typeList.find(item => this.data.type == item.code).tag;
      }
    }
  }
</script>

<style scoped lang="scss">

  .c-myForm {
    .el-form-item {
      margin-bottom: 22px;
    }
    .el-select, .el-cascader {
      width: 100%;
    }
    .el-checkbox-group {
      margin-left: -30px;
    }
    .c-checkbox {
      min-width: 78px;
    }
  }

  .c-map {
    width: 100%;
    display: flex;
    height: 100px;
    padding-left: 120px;
    margin-bottom: 5px;
    .c-map-body {
      border: 1px solid #efefef;
      width: 100%;
    }
  }

</style>



